.footer {
  background-color: #219EBC;
  color: #023047;
  position: fixed;
  left: 0;
  bottom: -1.75rem;
  width: 100%;
  text-align: center;
}
.footer:hover {
  transition: all 0.5s;
  bottom: 0;
}
@media (max-width: 768px) {
  .footer {
    bottom: -3.25rem;
  }
}
@media (max-width: 480px) {
  .footer {
    bottom: -4rem;
  }
}

.legalese {
  font-size: xx-small;
}
.legalese:hover {
  transition: all 0.5s;
  font-size: medium;
}

/* VARIABLES */
/* Foreground Colors */
/* Background Colors */
/* Spacing */
/* GRID */
/* Breakpoints */
.head {
  background-color: #219EBC;
  color: #023047;
  top: 0;
  width: 100%;
  text-align: center;
  flex: in-line;
  box-shadow: 0px 5px 8px #6d6a75;
}

.cats {
  list-style: none;
  text-decoration: none;
  color: #023047;
  display: none;
  padding: 0;
  font-size: large;
}
.cats a {
  text-decoration: none;
  color: #023047;
}
.cats a:hover {
  color: #6d6a75;
}

.row {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.log {
  display: flex;
  flex-direction: column;
}

.searchList {
  display: none;
}

/* VARIABLES */
/* Foreground Colors */
/* Background Colors */
/* Spacing */
/* GRID */
/* Breakpoints */
form {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.formParent {
  text-align: center;
  margin: 2rem 0 2rem 0;
  background-color: #219EBC;
  border-radius: 15px;
  box-shadow: 5px 5px 8px #6d6a75;
  padding: 0 1rem 1rem 1rem;
}

/* VARIABLES */
/* Foreground Colors */
/* Background Colors */
/* Spacing */
/* GRID */
/* Breakpoints */
.card {
  border: 2px solid #61dafb;
  border-radius: 15px;
  margin: 1rem;
  width: 22rem;
  height: 25rem;
  background-color: #219EBC;
}
.card:hover {
  box-shadow: 8px 5px 5px #6d6a75;
  transform: rotate(2deg);
  transition: all 0.3s;
}

.card-img-top {
  height: 50%;
  border-bottom: 1px solid #61dafb;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}

.card-body {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 50%;
}

.card-title {
  margin: 0.25rem 0;
  font-size: large;
  font-weight: bolder;
}

.card-text {
  margin: 0.25rem 0;
  text-shadow: 2px 2px 5px #023047;
}

/* VARIABLES */
/* Foreground Colors */
/* Background Colors */
/* Spacing */
/* GRID */
/* Breakpoints */
select, .formField {
  height: 2.25rem;
  border-radius: 15px;
  background-color: white;
}
select:focus, .formField:focus {
  background-color: #eae6e5;
}

label {
  font-size: larger;
  font-weight: bold;
}

.form-container {
  margin-bottom: 6rem;
  margin-top: 3rem;
  background-color: #219EBC;
  padding: 0 1rem 1rem 1rem;
  border-radius: 15px;
  box-shadow: 5px 5px 8px #6d6a75;
}

/* VARIABLES */
/* Foreground Colors */
/* Background Colors */
/* Spacing */
/* GRID */
/* Breakpoints */
.itemDiv {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  height: 70vh;
}
@media (max-width: 768px) {
  .itemDiv {
    flex-direction: column;
  }
}

.singleCard {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.singleCard img {
  width: 80%;
  border-radius: 15px;
  box-shadow: 5px 5px 8px #6d6a75;
  transform: rotate(-3.5deg);
}
@media (max-width: 768px) {
  .singleCard {
    margin-top: 4rem;
  }
}
@media (max-width: 480px) {
  .singleCard {
    width: 90%;
    margin-top: 1rem;
  }
}

.info {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #219EBC;
  border-radius: 15px;
  padding: 1rem;
  border: 1px solid #61dafb;
  box-shadow: 5px 5px 8px #6d6a75;
  text-shadow: 2px 2px 5px #023047;
}
@media (max-width: 768px) {
  .info {
    width: 50%;
    margin-top: 1.5rem;
  }
}
@media (max-width: 480px) {
  .info {
    width: 75%;
    margin-top: 1.5rem;
  }
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  padding: 0;
  font-weight: 600;
  line-height: 1.5;
  font-size: 18px;
  letter-spacing: 0.1em;
  color: #023047;
}

html,
body {
  min-height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

main {
  margin: 0 auto;
  max-width: 100%;
}

.title {
  margin: 0.25rem 0;
  font-size: x-large;
  font-weight: bolder;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.btn-primary {
  padding: 0.5rem;
  margin: 0.5rem;
  background-color: #023047;
  border-radius: 10px;
  color: white;
  text-decoration: none;
  border: 1px solid #61dafb;
}
.btn-primary:hover {
  transition: all 0.2s;
  box-shadow: 5px 5px 5px #6d6a75;
  border: 1px solid #6d6a75;
  color: #eae6e5;
}

.itemContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 4rem;
}
@media (max-width: 768px) and (max-width: 480px) {
  .itemContainer {
    margin-top: 1.5rem;
  }
}

